import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import DashBoard from './components/dashboard/DashBoard.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import Groups from './views/groups/Groups.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '登录',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '仪表盘',
        iconCls: 'fa fa-home',
        leaf: true,//只有一个节点
        children: [
            { path: '/dashboard', component: DashBoard, name: '仪表盘'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '配置管理系统',
        iconCls: 'el-icon-menu',//图标样式class
        children: [
            { path: '/table', component: Table, name: 'zookeeper管理' },
            { path: '/form', component: Form, name: 'agent管理' },
            { path: '/user', component: user, name: 'feedback管理' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '名字空间服务',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/page4', component: Page4, name: '页面4' },
            { path: '/page5', component: Page5, name: '页面5' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '研发组管理',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/groups', component: Groups, name: '书城研发组' },
            { path: '/groups', component: Groups, name: '运营研发组' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'zyagent管理',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;



